import React, { Component, Suspense } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import { Home, About, Contact, Projects } from './common/importer';
import NoMatch from './screens/NoMatch';

const routes = [
  {
    path: "/home",
    component: Home
  },
  {
    path: "/about",
    component: About
  },
  {
    path: "/contact",
    component: Contact
  },
  {
    path: "/projects",
    component: Projects
  },
  {
    component: NoMatch
  }
];

function RouteComponent(route) {
  return (
    <Route
      path={route.path ? route.path : null}
      render={props => {
          return (
            <route.component {...props} />
          )
        }
      }
    />
  );
}

export default class Routes extends Component {
  render() {
    return (
      <div className="main-container">
        <Suspense fallback={<div>Loader..</div>}>
          <Router>
            <Switch>
              {routes.map((route, i) => (
                <RouteComponent key={i} {...route} />
              ))}
            </Switch>
          </Router>
        </Suspense>
      </div>
    )
  }
}